
.main {
  width: 100%;
  display: flex;
  justify-content: center;
}
.main .hello {
  width: 800px;
  border: 5px solid #28b4e6;
  padding: 20px 0;
  border-radius: 25px;
  min-height: 400px;
  background: #f3f3f3c2 url('../img/dominospizza.jpg');
  background-blend-mode: color;
  background-size: cover;
}
.main .hello .header {
  height: 100px;
  font-size: 2.5em;
}
.main .hello .description {
  font-size: 1.5em;
  padding: 0 5px;
}
.main .hello .buttons {
  display: flex;
  justify-content: center;
}
.main .hello .buttons .button {
  cursor: pointer;
  height: 50px;
  width: 200px;
  background-color: #28b4e6;
  font-size: 1.5em;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  color: #000;
  transition: background-color 200ms linear;
}
.main .hello .buttons .button:hover {
  background-color: #49cfff;
}
.main .test-quastion {
  margin: 20px;
  font-size: 2em;
}
.main .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main .content ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
.main .content ul li {
  display: flex;
  width: 100%;
  height: 50px;
  font-size: 1.6em;
  padding: 0;
  margin: 0;
  cursor: pointer;
  transition: background-color 100ms linear;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #7d7d7d;
}
.main .content ul li:hover {
  background-color: #fff;
  border-bottom: 2px solid #28b4e6;
}
.main .photo {
  width: 300px;
  height: 400px;
}
.main .pushkin {
  background: url('../img/pushkin.jpg') no-repeat;
}
.main .test-answer {
  font-size: 2em;
  margin-bottom: 10px;
}
.main .pizza {
  font-size: 1.6em;
}
.main .hide {
  display: none;
}



